#date-picker {
    display: flex;
    align-items: center;
    gap: 16px;
}

.date-label {
  display: block;
  font-weight: 600;
  color: #555;
  font-size: 16px;
  width: 100%;
}

.date-input {
  padding: 6px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  width: 100%;
  max-width: 160px;
  transition: border-color 0.3s;
}

.date-input:focus {
  outline: none;
  border-color: #667eea;
}

@media (max-width: 600px) {
  #date-picker {
    flex-direction: column;
    gap: 0;
  }
  .date-label {
    padding-left: 5px;
  }
}